@import "@theme/vars.scss";

.section-reasons {
    position: relative;
    background-color: $dark;
    padding-bottom: 0;

    &__header {
        position: absolute;
        top: 95px;
        right: 20px;
    }

    &__title {
        display: inline-flex;
        align-items: center;
        fill: $red;
        // color: #fff;
        text-align: right;
        line-height: 35px;

        .icon{
            width: 43px;
            height: 65px;
            margin-left: 5px;
            fill: #fff;
            transition: fill 0.35s ease 0.25s;

            &.is-active{
                fill: $red;
            }
        }

        span{
                
        }
    }

    &__inner {
        // opacity: 0; 
    }

    &__list {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        height: 792px;
        overflow: hidden;
        // margin-top: 300px;
        // justify-content: flex-end;
    }

    &__item {
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        padding: 40px;
        height: 100%;
        min-height: 100%;
        max-height: 100%;
        // margin-bottom: -392px;
        overflow: hidden;
        transition: transform 0.5s ease;
        // position: absolute;
        // top: 0;
        // left: 0;
        // right: 0;

        &:nth-of-type(1){
            background-color: $red;
            color: #fff;
        }

        
        &:nth-of-type(2){
            background-color: $yellow;
            color: $gray;
            top: 100px;
        }
        
        &:nth-of-type(3){
            background-color: $blue;
            color: #fff;
            top: 200px;
        }
        
        &:nth-of-type(4){
            background-color: #fff;
            color: $gray;
            top: 300px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }

        &:hover{
            margin-bottom: 0;
            // transform: translateY(392px);
        }
    }

    &__item:last-of-type &__item-body{
        opacity: 1;
    }

    &__item:hover &__item-body{
        opacity: 1;
        pointer-events: all;
        transition: opacity 0.5s ease 0.25s;
    }

    &__item:hover ~ &__item{
        transform: translateY(392px) !important;
    }

    &__item-header{
        margin-bottom: 25px;
    }

    &__item-title{

        img{
            height: 66px;
            width: auto;
            margin: 0 -8px;
        }
    }

    &__item-body {
        box-sizing: border-box;
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.5s ease;
    }

    &__item-content{
    }


    &__item-text {
        -webkit-line-clamp: 12;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
}