@import "@theme/vars.scss";

.fidesco {
    background-color: #fff;
    color: $gray;

    &__intro {
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: relative;
        color: #fff;
        background-color: #003399;
    }

    &__intro-inner{
        box-sizing: border-box;
        padding: 100px 20px 60px;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        // flex-grow: 1;
    }

    &__intro-logo {
        width: calc(100% - 20px);
        max-width: 440px;
        margin-bottom: 40px;
    }

    &__intro-subtitle {
        text-align: center;
        margin-bottom: 30px;
        line-height: 132%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px -20px;
    }

    &__intro-item {
        display: flex;
        flex-direction: column;
        margin: 0 15px 20px;

        a{
            color: #fff;
            font-weight: 400;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        font-size: 11px;
        line-height: 13px;
        margin-bottom: 16px;
    }

    &__intro-item-text{
        font-size: 11px;
        line-height: 13px;
        display: flex;
    }

    &__intro-bg{
        display: block;
        // position: absolute;
        flex-grow: 1;
        width: 100%;
        object-fit: cover;
    }

    &__screen {
        position: relative;
        display: block;
        padding: 0 20px;

        &--typography{
            margin-bottom: 60px;
        }

        &--1{
            overflow: hidden;
            background-color: $gray;

            &::after{
                content: "";
                width: 100%;
                height: 140px;
                background-color: #0D0D0D;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
            }
        }

        &--2 {
            padding: 0;
            height: 700px;
            overflow: hidden;
        }

        &--3{
        }

        &--4{
            padding: 0;
        }

        &--5{
      
        }

        &--6{
        
        }
    }

    &__screen--1 &__screen-img{

        &--1{
            width: 93.5%;
        }

        &--2{
            width: 20.5%;
            position: absolute;
            top: 165px;
            right: 20px;
        }
    }
    
    &__screen--2 &__screen-img{
        height: 100%;
        object-position: center top;
    }

    &__screen--3 &__screen-img{
        width: 96%;

        &--1{
            margin-bottom: 18px;
        }

        &--2{
            // width: 96%;
        }

        &--3{
            position: absolute;
            top: 165px;
            top: 50%;
            transform: translate(50%, -50%);
            right: 0;
        }
    }
        
    &__screen--4 &__screen-img{

    }      

    &__screen--4 &__header{
        padding-right: 20px;
        padding-left: 20px;
    }

    &__screen-inner{
        position: relative;
    }

    &__screen-img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
        z-index: 1;
    }

    &__header {
        display: flex;
        align-items: center;
        padding-top: 60px;
        margin-bottom: 40px;
    }

    &__header-title {
        font-size: 22px;
        line-height: 26px;
    }

    &__header-number {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        width: 60px;
        height: 60px;
        margin-right: 15px;
        background-size: 100%;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml,%3Csvg width='59' height='59' viewBox='0 0 59 59' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect y='29.5' width='41.7193' height='41.7193' rx='3.65959' transform='rotate(-45 0 29.5)' fill='white'/%3E%3Crect x='8.7832' y='30.0085' width='29.2767' height='29.2767' rx='3.65959' transform='rotate(-45 8.7832 30.0085)' fill='%230D7DFC'/%3E%3C/svg%3E%0A");

        span{
            position: relative;
            z-index: 2;
        }
        &::before{
            content: "";
            width: 42px;
            height: 42px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-45deg);
            box-shadow: -1.31654px 25.5329px 25.5256px rgba(45, 135, 219, 0.08), 0.731918px 0px 9.51493px rgba(45, 135, 219, 0.12), -0.731918px 2.19575px 3.38512px rgba(45, 135, 219, 0.08);
        }
    }

    &__typography {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__typography-img {
        display: block;
        width: 80%;
        max-width: 392px;
        object-fit: contain;
        margin-bottom: 30px;
    }

    &__typography-colors {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    }

    &__typography-color {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        border-radius: 50%;
        flex-shrink: 0;

        span{
            font-size: 8px;
            line-height: 10px;
        }

        &:nth-of-type(1){
            width: 132px;
            height: 132px;
            background-color: #EB5757;
        }

        &:nth-of-type(2){
            width: 108px;
            height: 108px;
            background-color: $gray;
            margin-left: -28px;
        }

        &:nth-of-type(3){   
            height: 90px;
            width: 90px;
            background-color: #191919;
            margin-left: -16px;
        }

    }

}
