@import "@theme/vars.scss";

.richness {
    background-color: #333333;
    color: #fff;

    &__intro {
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: relative;
        color: #fff;
    }

    &__intro-inner{
        box-sizing: border-box;
        padding: 30px 20px;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        flex-grow: 1;
    }

    &__intro-logo {
        width: 269px;
        height: 37px;
        margin-bottom: 18px;
    }

    &__intro-subtitle {
        margin-bottom: 18px;
        line-height: 150%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -20px -20px;
    }

    &__intro-item {
        margin: 0 20px 20px;

        a{
            color: #fff;
            font-weight: 400;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        margin-bottom: 10px;

        &:first-child{
            font-weight: 700;
            color: #DABE83;
        }

        &:last-child{
            font-weight: 500;
            margin-bottom: 0;
        }
    }

    &__intro-bg{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__screen {
        position: relative;
        display: block;

        &--typography{

        }

        &--1{
            overflow: hidden;
            height: 722px;
            background-color: #DABE83;
        }

        &--2 {
            padding: 100px 20px;
        }

        &--3{
            height: 100vh;
        }

        &--4{
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: #fff;
            margin-bottom: 100px;

            &::after{
                content: "";
                width: 100%;
                height: 64px;
                background-color: #333333;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
            }
        }

        &--5{
            height: 627px;
            position: relative;
            margin-bottom: 100px;
        }

        &--6{
            overflow: hidden;
            height: 715px;
            padding-bottom: 100px;
        }
    }

    &__screen--typography &__haeder{
        margin-bottom: 150px;
    }

    &__screen--1 &__screen-img{
        height: 722px;
        width: auto;
        object-position: top center;
        object-fit: cover;
    }

    &__screen--2 &__screen-img{
        box-sizing: border-box;
        width: 100%;
        max-height: 100vh;
        margin-bottom: -14%;
        object-fit: contain;
    }

    &__screen--3 &__screen-img{
        height: 100%;
        width: auto;
        min-width: 130%;
        max-width: none;
        object-fit: cover;
    }

    &__screen--4 &__screen-img{
        position: relative;
        z-index: 1;
    }

    &__screen--4 &__screen-img--1{
        position: absolute;
        width: 204px;
        height: 177px;
        top: 86px;
        right: -30px;
    }

    &__screen--4 &__screen-img--2{
        margin-right: -18.6%;
        margin-bottom: -18%;
        width: 114%;
        // filter: drop-shadow(37.0286px 26.3049px 36.3368px rgba(0, 0, 0, 0.13)) drop-shadow(21.8024px 15.4883px 18.9533px rgba(0, 0, 0, 0.13)) drop-shadow(11.1975px 7.9546px 8.89524px rgba(0, 0, 0, 0.13)) drop-shadow(4.50268px 3.19867px 3.8953px rgba(0, 0, 0, 0.13)) drop-shadow(1.00718px 0.715493px 1.68603px rgba(0, 0, 0, 0.13));
    }

    &__screen--4 &__header{
        color: $gray
    }

    &__screen--5 &__screen-img{
        position: absolute;
        top: 0;
        right: 0;
        margin-top: -20px;
        width: 609px;
        height: 770px;
        object-fit: contain;
        transform: translateX(24%);
    }

    &__screen--6 &__screen-img{
        position: absolute;
        top: 0;
        margin-top: -25px;
        height: 880px;
        width: 696px;
        left: 0;
        object-fit: contain;
        transform: translateX(-24%);
    }

    &__screen-img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        padding: 60px 20px 0;
        margin-bottom: 100px;
    }

    &__header-title {
    }

    &__header-number {
    }

    &__typography {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__typography-img {
        display: block;
        width: 100%;
        max-width: 392px;
        object-fit: contain;
        margin-bottom: 70px;
    }

    &__typography-colors {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__typography-color {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 50%;
        height: 208px;
        background-color: #fff;
        color: $gray;

        span{
            margin-bottom: 10px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        &:nth-of-type(1){
            background-color: #333;
            color: #BFBFBF;
        }

        &:nth-of-type(2){
            background-color: #fff;
        }

        &:nth-of-type(3){   
            background-color: #292929;
            color: #BFBFBF;
        }

        &:nth-of-type(4){
            background-color: #DABE83;
        }
    }

}
