@import "@theme/vars.scss";

button,
a {
    padding: 0;
    background: none;
    border: none;
    outline: none;
}

.button {
    z-index: 1;
    cursor: pointer;
    padding: 0;
    background: none;
    outline: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    box-sizing: border-box;
    padding: 0 30px;
    border: 1px solid #000;
    border-radius: 10px;
    font-family: 'IBMPlexMono';
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    color: $gray;
    fill: $gray;
    background-color: #fff;
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease;

    &__text{
        
    }

    &__icon{
        flex-shrink: 0;
        width: 30px;
        height: 30px;
    }

    &__text + &__icon{
        margin-left: 10px;
    }

    &__icon &__text{
        margin-left: 10px;
    }

    &:hover{
        background-color: $gray;
        border-color: $gray;
        fill: #fff;
        color: #fff;
    }

    &[disabled]{
        pointer-events: none;
        opacity: 0.5;
    }


    // &--border{
    //     color: $yellow;
    //     fill: $yellow;
    //     background-color: transparent;

    //     &:hover{
    //         background-color: $yellow;
    //         border-color: $yellow;
    //         color: #fff;
    //         fill: #fff;
    //     }
    // }

    &--gray{
        background-color: $gray;
        border-color: $gray;
        fill: #fff;
        color: #fff;

        &:hover{
            color: $gray;
            fill: $gray;
            background-color: #fff;
            border-color: #fff;
        }
    }

    &--border#{&}--white{
        border-color: #fff;
        fill: #fff;
        color: #fff;
        background-color: transparent;

        &:hover{
            border-color: #fff;
            background-color: #fff;
            color: $yellow;
            fill: $yellow;
        }
    }

    &--icon{
        padding: 0;
        width: 54px;
    }

    // input{
    //     display: none;
    // }
}